    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 4fr;
}

/* List container */

.list-container h1 {
    font-family: 'robotobold'; 
    text-align: center;
    margin: 16px auto;
    background-color: rgb(20, 92, 247);
    color: white;
    width: 150px;
    padding: 8px;
    border-radius: 0px 25px 25px 25px;
}

/* List nav */

.list-nav {
    display: flex;
    flex-direction: column;
}

.list-button {
    text-align: left;
    border: none;
    background-color: white;
    margin: 0 12px;
    color: rgb(20, 92, 247);
    border-radius: 8px;
    padding-left: 8px;
    transition: background-color 0.25s ease;
}

.new-list-button.special {
    font-family: 'robotobold'; 
    color: rgb(20, 92, 247);
}

.list-button.special {
    color: rgb(60, 119, 246);
}

.list-button:hover {
    background-color: rgb(241, 241, 241);
    cursor: pointer;
}

.list-button:focus {
    outline: none;
}

.selected {
    background-color: rgb(241, 241, 241);
}

/* Task container */

.task-container {
    padding: 16px;
}

.new-task-button {
    background-color: rgb(20, 92, 247);
    color: white;
    border: none;
    width: 64px;
    height: 64px;
    border-radius: 100%;
    padding: 0;
    font-size: 2rem;
    transition: transform 0.3s ease;
}

.new-task-button:hover {
    cursor: pointer;
    transform: scale(1.04);
}

.new-task-button:active {
    cursor: pointer;
    transform: scale(0.96);
    background-color: rgb(36, 86, 195);
}

/* List div */

.list-div {
    padding-top: 16px;
    display: grid;
}

.task-element {
    height: 64px;
    display: grid;
    grid-template-columns: 8px 48px 1fr 148px;
    border-radius: 8px;
    overflow: hidden;
    transition: background-color 0.25s ease;
}

.task-element:hover {
    background-color: rgb(241, 241, 241);
}

.buffer-element {
    padding: 16px;
    border-top: solid rgb(228, 228, 228) 1px;
}

input[type="checkbox"] {
    width: 20px;
    height: 20px;
    align-self: center;
    justify-self: center;
}

input[type="checkbox"]:hover {
    cursor: pointer;
}

.task-element>p {
    align-self: center;
    justify-self: center;
}

.text-div {
    align-self: center;
}

.priority-1 {
    background-color: red;
}

.priority-2 {
    background-color: rgb(255, 245, 47);
}

.priority-3 {
    background-color: rgb(46, 223, 140);
}

/* Dialog boxes */
  
dialog {
    margin: auto;
    border: 0;
    border-radius: 24px;
    padding: 32px 64px;
    box-shadow: 0px 0px 10px 2px rgb(175, 175, 175);
}

dialog form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
}

legend {
    margin-bottom: 4px;
    font-size: 1.8rem;
    font-family: 'robotobold';
}

label {
    display: block;
    text-align: center;
    font-family: 'robotobold';
}

.btn-div {
    margin-top: 12px;
}

.btn-div button {
    background-color: rgb(20, 92, 247);
    color: white;
    width: 72px;
    height: 38px;
    border: 0;
    border-radius: 25px;
    margin: auto 8px;
    transition: background-color 0.3s ease;
}

.btn-div button:hover {
    cursor: pointer;
    background-color: rgb(45, 105, 235);
}

.btn-div button:active {
    cursor: pointer;
    background-color: rgb(36, 86, 195);
    scale: 98%;
}

form input,
form textarea,
form select {
    border: 0;
    background-color: whitesmoke;
    padding: 4px 16px;
    border-radius: 25px;
    transition: background-color 0.3s ease;
}

form textarea {
    padding: 8px 16px;
    resize: none;
}

form input:hover,
form textarea:hover,
form select:hover {
    background-color: rgb(222, 220, 220);
}

form input:focus,
form textarea:focus,
form select:focus {
    background-color: rgb(222, 220, 220);
    outline: 0px;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,uBAAuB;IACvB,8BAA8B;AAClC;;AAEA,mBAAmB;;AAEnB;IACI,yBAAyB;IACzB,kBAAkB;IAClB,iBAAiB;IACjB,kCAAkC;IAClC,YAAY;IACZ,YAAY;IACZ,YAAY;IACZ,iCAAiC;AACrC;;AAEA,aAAa;;AAEb;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,uBAAuB;IACvB,cAAc;IACd,uBAAuB;IACvB,kBAAkB;IAClB,iBAAiB;IACjB,uCAAuC;AAC3C;;AAEA;IACI,yBAAyB;IACzB,uBAAuB;AAC3B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,oCAAoC;IACpC,eAAe;AACnB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,oCAAoC;AACxC;;AAEA,mBAAmB;;AAEnB;IACI,aAAa;AACjB;;AAEA;IACI,kCAAkC;IAClC,YAAY;IACZ,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,mBAAmB;IACnB,UAAU;IACV,eAAe;IACf,+BAA+B;AACnC;;AAEA;IACI,eAAe;IACf,sBAAsB;AAC1B;;AAEA;IACI,eAAe;IACf,sBAAsB;IACtB,kCAAkC;AACtC;;AAEA,aAAa;;AAEb;IACI,iBAAiB;IACjB,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,yCAAyC;IACzC,kBAAkB;IAClB,gBAAgB;IAChB,uCAAuC;AAC3C;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,aAAa;IACb,wCAAwC;AAC5C;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,mCAAmC;AACvC;;AAEA,iBAAiB;;AAEjB;IACI,YAAY;IACZ,SAAS;IACT,mBAAmB;IACnB,kBAAkB;IAClB,+CAA+C;AACnD;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,yBAAyB;AAC7B;;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,yBAAyB;AAC7B;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,kCAAkC;IAClC,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,SAAS;IACT,mBAAmB;IACnB,gBAAgB;IAChB,sCAAsC;AAC1C;;AAEA;IACI,eAAe;IACf,mCAAmC;AACvC;;AAEA;IACI,eAAe;IACf,kCAAkC;IAClC,UAAU;AACd;;AAEA;;;IAGI,SAAS;IACT,4BAA4B;IAC5B,iBAAiB;IACjB,mBAAmB;IACnB,sCAAsC;AAC1C;;AAEA;IACI,iBAAiB;IACjB,YAAY;AAChB;;AAEA;;;IAGI,oCAAoC;AACxC;;AAEA;;;IAGI,oCAAoC;IACpC,YAAY;AAChB","sourcesContent":["body {\n    display: grid;\n    grid-template-rows: 1fr;\n    grid-template-columns: 1fr 4fr;\n}\n\n/* List container */\n\n.list-container h1 {\n    font-family: 'robotobold'; \n    text-alig